import React from 'react';
import {Text, View, ScrollView} from 'react-native';
import {colors, spacings} from '@mobile/tokens';
import styled from 'styled-components/native';

type ColorsCompProps = {
  color: string;
  keyColor?: string;
};

const redShades = Object.entries(colors.red.shades);
const greenShade = Object.entries(colors.green.shades);
const pearShade = Object.entries(colors.pear.shades);
const grayShade = Object.entries(colors.gray.shades);

const ColorBox = styled(View)<ColorsCompProps>`
  width: 100px;
  height: 100px;
  background-color: ${({color}) => color};
  border-radius: 5px;
  margin-bottom: ${spacings.regular};
  padding: ${spacings.small}px;
  text-transform: uppercase;
`;

const SectionTitle = styled(View)`
  font-size: ${spacings.xxLarge}px;
`;

const ColorsContainer = styled(View)`
  display: flex;
  flex-direction: row;
  gap: ${spacings.large};
  flex-wrap: wrap;
  margin-bottom: ${spacings.regular}px;
  margin-top: ${spacings.regular}px;
  width: 100%;
`;

export const Color: React.FC<ColorsCompProps> = props => {
  return (
    <View>
      <ColorBox color={props.color} />
      <Text>
        {props.keyColor}: {props.color}
      </Text>
    </View>
  );
};

export const Colors = () => {
  return (
    <ScrollView scrollEnabled style={{maxWidth: 1200}}>
      <SectionTitle>RED TOKENS</SectionTitle>
      <ColorsContainer>
        <Color color={colors.red.primary} keyColor="primary" />
        {redShades.map(([shade, value]) => (
          <Color key={shade} keyColor={shade} color={value} />
        ))}
      </ColorsContainer>
      <SectionTitle>GREEN TOKENS</SectionTitle>
      <ColorsContainer>
        <Color color={colors.green.primary} keyColor="primary" />
        {greenShade.map(([shade, value]) => (
          <Color key={shade} keyColor={shade} color={value} />
        ))}
      </ColorsContainer>
      <SectionTitle>GREEN TOKENS</SectionTitle>
      <ColorsContainer>
        <Color color={green.primary} keyColor="primary" />
        {greenShade.map(([shade, value]) => (
          <Color key={shade} keyColor={shade} color={value} />
        ))}
      </ColorsContainer>
      <SectionTitle>PEAR TOKENS</SectionTitle>
      <ColorsContainer>
        <Color color={pear.primary} keyColor="primary" />
        {pearShade.map(([shade, value]) => (
          <Color key={shade} keyColor={shade} color={value} />
        ))}
      </ColorsContainer>
      <SectionTitle>GRAY TOKENS</SectionTitle>
      <ColorsContainer>
        <Color color={gray.primary} keyColor="primary" />
        {grayShade.map(([shade, value]) => (
          <Color key={shade} keyColor={shade} color={value} />
        ))}
      </ColorsContainer>
    </ScrollView>
  );
};
